// Tipografía

@font-face {
    font-family: 'CodePro';
    src: url('../fonts/CamingoDosProCd-SemiBold.otf') format('opentype'); 
}

@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf') format('truetype'); 
}

@font-face {
    font-family: 'MontserratBold';
    src: url('../fonts/Montserrat-Bold.ttf') format('truetype'); 
}

@font-face {
    font-family: 'MontserratSemiBold';
    src: url('../fonts/Montserrat-SemiBold.ttf') format('truetype'); 
}

@font-face {
    font-family: 'Palace';
    src: url('../fonts/Palace.ttf') format('truetype'); 
}

@font-face {
    font-family: 'CamingoDosProCd-SemiBold';
    src: url('../fonts/CamingoDosProCd-SemiBold.otf') format('opentype'); 
}

@font-face {
    font-family: 'Fontastique';
    src: url('../fonts/Fontastique.ttf') format('truetype'); 
}

@font-face {
    font-family: 'Sansation';
    src: url('../fonts/Sansation.ttf') format('truetype'); 
}

// Fonts

$text: 'Montserrat';
$textBold: 'MontserratBold';
$textSemiBold: 'MontserratSemiBold';
$title: 'CodePro';
$subtitle: 'Fontastique';
$textNarrow: 'CamingoDosProCd-SemiBold';


// Colores

$white: #ffffff;
$primaryBlue: #DC7DA0;
$primaryBlueDark: #DC7DA0;
$primaryPurple: #141b41;
$secondaryGray: #c9b7ae;
$secondaryGrayDark: #a08d87;
$redBtn: #c22a2a;
$gray: #767676;
$pink: #d87f92;
$grayBackground: #dfe7ec;

// Tamaños de Media Queries

$mobile: 320px;
$tablet: 481px;
$laptop: 768px;
$desktop: 1025px;
$desktopXL: 1201px;
$desktopXXL: 1408px;