@use "base/variables" as v;
@use "base/mixins" as m;

.catalogue {
  margin: inherit 2rem;

  @include m.tablet {
    margin: inherit 3rem;
  }

  @include m.desktopXL {
    max-width: 70%;
    margin: inherit auto;
  }
}

.donant-info {
  background-color: v.$primaryBlueDark;

  @include m.tablet {
    background-color: v.$white;
  }

  .hero-img {
    display: none;

    @include m.tablet {
      display: block;
    }
  }
}

.catalogue-buttons {
  background-color: v.$primaryBlueDark;
  color: v.$white;
  margin: 1rem auto;
  margin-top: 0;
  padding-bottom: 5rem;
  padding-top: 5rem;

  @include m.tablet {
    margin: 5rem auto;
    margin-top: 0;
    padding-top: 5rem;
  }

  @include m.tablet {}

  @include m.desktop {
    max-width: 70%;
    margin: 20rem auto;
    margin-top: 0;
    padding-top: 5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.catalogue-buttons-phenotype {
  background-color: v.$primaryBlueDark;
  color: v.$white;
  margin: 1rem auto;
  margin-top: 0;
  padding-bottom: 5rem;
  padding-top: 0;

  @include m.tablet {
    margin: 5rem auto;
    margin-top: 0;
    padding-top: 0;
  }

  @include m.tablet {}

  @include m.desktop {
    // max-width: 70%;
    margin: 5rem;
    margin-top: 0;
    padding-top: 0;
    padding-bottom: 10rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.phenotype-code {
  color: v.$primaryBlueDark;
  background-color: v.$white;
  padding: 2rem 3rem;
  border-radius: 2rem;
  @include m.tablet {
    margin: 3rem 0;
  }
}

.catalogue-button {
  display: flex;
  justify-content: center;

  a {
    border: 1px solid #fff;
    margin-left: auto;
    margin-right: auto;
    padding: 0.5rem 1rem;
    transition: all .25s;
    padding: 0.5rem 1rem;
    margin-left: auto;
    margin-right: auto;
    color: v.$white;

    &:hover {
      color: v.$white;
      background-color: darken(v.$primaryBlueDark, 5%);
      transition: all 0.25s;
    }
    margin: 2rem;

    @include m.mobile {
      font-size: 1rem;
    }

    @include m.tablet {
      font-size: 1.15rem;
    }

    @include m.laptop {
      font-size: 1.35rem;
    }

    @include m.desktop {
      font-size: 1.75rem;
      padding: 1rem 2rem;
    }

    @include m.desktopXL {
      font-size: 2rem;
      padding: 1rem 3rem;
    }
  }
}

.return-button {
