.donants {
  .donant-extra {
    display: flex;
    align-items: center;
    justify-content: center;
    background: v.$primaryBlueDark;
    color: v.$white;
    margin: 1rem auto;
    margin-bottom: 0;
    padding: 5rem;

    @include m.tablet {
      padding: unset;
      margin: 5rem auto;
      margin-bottom: 0;
    }

    @include m.desktop {
      max-width: 70%;
      margin: 20rem auto;
      margin-bottom: 0;
    }

    @include m.tablet {
      padding: 2rem;
      padding-top: 3.5rem;
    }

    @include m.laptop {
      padding: 5rem;
      padding-top: 7.5rem;
    }

