      li {
        list-style: disc;
      }
    }

    text-align: center;

    @include m.tablet {
      text-align: left;
    }

    ul {
      li {
        @include m.tablet {
          font-size: 2.2vw;
        }

        @include m.laptop {
          font-size: 2vw;
        }

