      @include m.tablet {
        padding: 35%;
      }
    }

    display: grid;
    background-color: v.$primaryBlueDark;
    color: v.$white;
    grid-template-columns: 1fr;
    gap: 2rem;
    margin: 1rem auto;
    margin-bottom: 0;
    padding: 5rem;

    @include m.tablet {
      padding: unset;
      margin: 5rem auto;
      margin-bottom: 0;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: repeat(7, 1fr);
      grid-column-gap: 12px;
      grid-row-gap: 12px;
    }

    @include m.desktop {
      max-width: 70%;
      margin: 20rem auto;
      margin-bottom: 0;
    }

    @include m.tablet {
      .div1 {
        grid-area: 1 / 1 / 5 / 4;
      }

      .div2 {
        p {
          background-color: v.$white;
          color: v.$primaryBlueDark;
          padding: 1rem 2rem;
          margin: unset;
          margin-top: 2rem;
          margin-bottom: 4rem;
          border-radius: 2rem;
        }

        grid-area: 1 / 4 / 5 / 7;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        @include m.tablet {
          padding: 2rem;
          padding-top: 3.5rem;
        }

        @include m.laptop {
          padding: 5rem;
          padding-top: 7.5rem;
        }

        ul {
          li {
            list-style: disc;
          }
        }
      }

      .div3 {
        grid-area: 5 / 1 / 8 / 3;
      }

      .div4 {
        grid-area: 5 / 3 / 8 / 5;
      }

      .div5 {
        grid-area: 5 / 5 / 8 / 7;
      }
    }

    .div2 {
      text-align: center;

      @include m.tablet {
        text-align: left;
      }

      ul {
        li {
          @include m.tablet {
            font-size: 2.2vw;
          }

          @include m.laptop {
            font-size: 2vw;
          }

          @include m.desktop {
            font-size: 1.5vw;
          }
        }
      }
    }
  }

  .phenotype-parent {
    display: grid;
    grid-template-columns: 1fr;
    // grid-template-rows: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr 1fr 7fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    background-color: v.$primaryBlueDark;
    color: v.$white;
    // margin: 5rem auto;
    padding: 1rem;
    max-width: 100%;
    gap: 2rem;

    .div1,
    .div2,
    .div3 {
      height: 20vw;
      width: 70%;
      margin-left: auto;
      margin-right: auto;
    }

    @include m.mobile {
      grid-template-rows: 1fr 1fr 1fr 5fr;
    }

    @include m.tablet {
      margin: auto;
      grid-template-rows: 1fr 1fr 1fr 3fr;
      gap: 2rem;
      // max-width: 70%;
      padding: 5rem;
      // .div1,
      // .div2,
      // .div3 {
      //   height: 20rem;
      // }
    }

    @include m.laptop {
      margin: auto;
      gap: 1rem;
      max-width: 100%;
      padding: 5rem 10rem;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);

      .div1,
      .div2,
      .div3 {
        height: 11vw;
      }

      .div1 {
        grid-area: 1 / 1 / 2 / 2;
        margin-top: auto;
      }

      .div2 {
        grid-area: 2 / 1 / 3 / 2;
        margin-bottom: auto;
        margin-top: auto;
      }

      .div3 {
        grid-area: 3 / 1 / 4 / 2;
      }

      .div4 {
        grid-area: 1 / 2 / 4 / 3;
      }
    }

    @include m.desktop {
      margin: 5rem;
      margin-bottom: 0;
      padding: 5rem 15rem;
      // .div1,
      // .div2,
      // .div3 {
      //   // height: 20rem;
      // }
    }

    @include m.desktopXL {
      padding: 5rem 20rem;
      // max-width: 70%;
      // .div1,
      // .div2,
      // .div3 {
      //   height: 25rem;
      // }
    }

    .donant-data {
      margin: 0 auto;

    }
  }

  img {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
}

/* Center website */
.catalogue {
  margin-bottom: 10rem;

  .main {
    max-width: 1000px;
    margin: auto;
  }

  h1 {
    font-size: 50px;
    word-break: break-all;
  }

  .row {
    display: grid;
    gap: 0.5rem;
    grid-template-columns: repeat(1, 1fr);

    @include m.tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include m.laptop {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  /* Add padding BETWEEN each column (if you want) */
  .row,
  .row>.column {
    padding: 0;
  }

  /* Create three equal columns that floats next to each other */
  .column {
    float: left;
    display: none;
    /* Hide columns by default */
    margin: inherit 0.5rem;

    h4 {
      margin: 2rem;
      margin-bottom: 0;
      padding: 1rem;
      text-align: center;
    }

    p {
      margin: 0;
    }

    .catalogue-img-container {
      .catalogue-img {
        height: 95vw;
        width: 100%;
        object-fit: cover;

        @include m.tablet {
          height: 50vw;
        }

        @include m.laptop {
          height: 30vw;
        }

        @include m.desktopXL {
          height: 25vw;
        }
      }
    }

    transition: all 0.5s;

    &:hover {
      box-shadow: 0px 0px 11px 2px rgba(0, 0, 0, 0.58);
      -webkit-box-shadow: 0px 0px 11px 2px rgba(0, 0, 0, 0.58);
      -moz-box-shadow: 0px 0px 11px 2px rgba(0, 0, 0, 0.58);
      transition: all 0.5s;
    }
  }

  /* Clear floats after rows */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }

  /* Content */
  .content {
    background-color: white;
    padding: 10px;
  }

  /* The "show" class is added to the filtered elements */
  .show {
    display: block;
  }

  /* Style the buttons */
  .btn {
    border: none;
    outline: none;
    padding: 12px 16px;
    background-color: white;
    cursor: pointer;
  }

  /* Add a grey background color on mouse-over */
  .btn:hover {
    background-color: darken(v.$primaryBlue, 20%);
    color: v.$white;
  }

  /* Add a dark background color to the active button */
  .btn.active {
    background-color: #666;
    color: white;
  }

  .img-1-pre,
  .img-2-pre,
  .img-3-pre,
  .img-4-pre {
    margin-left: 1rem;
    margin-top: 1rem;
    height: 25rem;
  }

  .catalogue-btn {
    font-family: v.$text;
    font-size: 1.5rem;
    margin: 0.1rem;

    @include m.tablet {
      font-size: 2rem;
    }
  }

  #myBtnContainer {
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;

    @include m.tablet {
      justify-content: flex-start;
    }
  }
}

