@use "base/variables" as v;
@use "base/mixins" as m;

.logout {
  display: flex;
  justify-content: center;
  a {
    background-color: v.$redBtn;
    color: v.$white;
    text-transform: uppercase;
    //   font-family: v.$text;
    padding: 0.5rem 3rem;
    border-radius: 2rem;
    transition: all 0.5s;
    &:hover {
      background-color: darken(v.$redBtn, 10%);
      color: v.$white;
      transition: all 0.5s;
    }
    @include m.tablet {
      padding: 1rem 6rem;
    }
  }
}

.create-user {
  display: flex;
  justify-content: center;
  // margin: 4rem;
  a {
    background-color: v.$primaryBlueDark;
    color: v.$white;
    text-transform: uppercase;
    //   font-family: v.$text;
    padding: 0.5rem 3rem;
    border-radius: 2rem;
    transition: all 0.5s;
    &:hover {
      background-color: darken(v.$primaryBlueDark, 10%);
      color: v.$white;
      transition: all 0.5s;
    }
    @include m.tablet {
      padding: 1rem 6rem;
    }
  }
}

// Breakpoints
$bp-maggie: 1201px;
$bp-lisa: 1201px;
$bp-bart: 1201px;
$bp-marge: 1201px;
$bp-homer: 1201px;

// Styles

a {
  color: v.$primaryBlue;

  &:hover,
  &:focus {
    color: v.$primaryBlue;
  }
}

.container {
  margin: 5% auto;

  @media (min-width: $bp-bart) {
    margin: 2% auto;
    max-width: 95%;
  }

  @media (min-width: $bp-homer) {
    margin: 2em auto;
    // max-width: 110em;
  }
}

.responsive-table {
  width: 100%;
  margin-bottom: 1.5em;
  border-spacing: 0;

  @media (min-width: $bp-bart) {
    font-size: 0.9em;
  }

  @media (min-width: $bp-marge) {
    font-size: 1em;
  }

  thead {
    // Accessibly hide <thead> on narrow viewports
    position: absolute;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden;

    @media (min-width: $bp-bart) {
      // Unhide <thead> on wide viewports
      position: relative;
      clip: auto;
      height: auto;
      width: auto;
      overflow: auto;
    }

    th {
      background-color: v.$primaryBlueDark;
      border: 1px solid v.$primaryBlueDark;
      font-weight: normal;
      text-align: center;
      color: white;
      font-size: 1.1rem;

      @include m.desktop {
        font-size: 1.4rem;
      }

      &:first-of-type {
        text-align: left;
      }
    }
  }

  // Set these items to display: block for narrow viewports
  tbody,
  tr,
  th,
  td {
    display: block;
    padding: 0;
    text-align: left;
    white-space: normal;
  }

  tr {
    @media (min-width: $bp-bart) {
      // Undo display: block
      display: table-row;
    }
  }

  th,
  td {
    padding: 0.5em;
    vertical-align: middle;

    @media (min-width: $bp-lisa) {
      padding: 0.75em 0.5em;
    }

    @media (min-width: $bp-bart) {
      // Undo display: block
      display: table-cell;
      padding: 0.5em;
    }

    @media (min-width: $bp-marge) {
      padding: 0.75em 0.5em;
    }

    @media (min-width: $bp-homer) {
      padding: 0.75em;
    }
  }

  caption {
    margin-bottom: 1em;
    font-size: 1em;
    font-weight: bold;
    text-align: center;
    margin-top: 2rem;

    @media (min-width: $bp-bart) {
      font-size: 1.5em;
    }
  }

  tfoot {
    font-size: 0.8em;
    font-style: italic;

    @media (min-width: $bp-marge) {
      font-size: 0.9em;
    }
  }

  tbody {
    @media (min-width: $bp-bart) {
      // Undo display: block
      display: table-row-group;
    }

    tr {
      margin-bottom: 1em;

      @media (min-width: $bp-bart) {
        // Undo display: block
        display: table-row;
        border-width: 1px;
      }

      &:last-of-type {
        margin-bottom: 0;
      }

      &:nth-of-type(even) {
        @media (min-width: $bp-bart) {
          background-color: rgba(0, 0, 0, 0.12);
        }
      }
    }

    th[scope="row"] {
      background-color: v.$primaryBlue;
      color: white;
      text-align: center;
      font-size: 1.1rem;
      @media (min-width: $bp-lisa) {
        border-left: 1px solid v.$primaryBlueDark;
        border-bottom: 1px solid v.$primaryBlueDark;
      }

      @media (min-width: $bp-bart) {
        background-color: transparent;
        color: rgba(0, 0, 0.87);
        text-align: left;
      }

      @include m.desktop {
        font-size: 1.4rem;
      }
    }

    td {
      a {
        &:hover {
          font-weight: bold;
        }
      }
      text-align: right;
      font-size: 1.1rem;
      @media (min-width: $bp-bart) {
        border-left: 1px solid v.$primaryBlueDark;
        border-bottom: 1px solid v.$primaryBlueDark;
        text-align: center;
      }
      @include m.desktop {
        font-size: 1.4rem;
      }

      &:last-of-type {
        @media (min-width: $bp-bart) {
          border-right: 1px solid v.$primaryBlueDark;
        }
      }
      form {
        padding-right: 0;
        padding-left: 0;
      }
    }

    td[data-type="currency"] {
      text-align: right;
    }

    td[data-title]:before {
      content: attr(data-title);
      float: left;
      font-size: 0.8em;
      color: rgba(0, 0, 0, 0.54);

      @media (min-width: $bp-lisa) {
        font-size: 0.9em;
      }

      @media (min-width: $bp-bart) {
        // Don’t show data-title labels
        content: none;
      }
    }
  }
}

.boton-rojo-block {
  background-color: transparent;
  border: none;
  color: v.$redBtn;
  &:hover {
    font-weight: bold;
  }
}

.menu-users {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  width: 75%;
  margin: 5rem;
  margin-bottom: 0rem;
  margin-top: 4rem;
  margin-left: auto;
  margin-right: auto;
  @include m.tablet {
    flex-direction: row;
    width: 100%;
  }
  @include m.laptop {
    width: 75%;
  }
  @include m.laptop {
    margin: 10rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    margin-top: 5rem;
  }
  & > * {
    a {
      font-weight: bold;
      font-size: 1.25rem;
      margin-bottom: 2rem;
      @include m.tablet {
        font-size: 1.2rem;
      }
      @include m.desktop {
        font-size: 2rem;
      }
    }
  }
}

.responsive-table tbody td {
  font-size: 1.25rem;
  @include m.tablet {
    font-size: 1.5rem;
  }
  @include m.laptop {
    font-size: 1.75rem;
  }
}

.responsive-table thead th {
  font-size: 1.45rem;
  @include m.tablet {
    font-size: 1.75rem;
    font-weight: bold;
  }
  @include m.laptop {
    font-size: 2rem;
  }
  cursor: pointer;
}

.responsive-table tbody td[data-title]:before {
  font-size: 1em;
}

.responsive-table tbody th[scope="row"] {
  font-size: 1.35rem;
  @include m.tablet {
    font-size: 1.65rem;
  }
  @include m.laptop {
    font-size: 2rem;
  }
}

.responsive-table caption {
  font-size: 1.15rem;
  @include m.tablet {
    font-size: 1.4rem;
  }
  @include m.laptop {
    font-size: 1.5rem;
  }
}

.input-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m.mobile {
    flex-direction: row;
  }
}

.thead {
  background-color: v.$primaryBlueDark;
  border: 1px solid v.$primaryBlueDark;
  color: v.$white;
  display: none !important;
  @include m.desktopXL {
    display: table-row !important;
  }
  th {
    cursor: pointer;
    font-size: 1.45rem;
    text-align: center;
    @include m.tablet {
      font-size: 1.75rem;
      font-weight: bold;
    }
    @include m.laptop {
      font-size: 2rem;
    }
  }
}

label[for="searchBox"] {
  color: v.$primaryBlueDark;
  font-weight: bold;
}

input[type="search"] {
  border: 1px solid v.$primaryBlueDark;
  margin-left: 1rem !important;
  font-size: 1.5rem;
  padding: 0.75rem;
  &:focus-visible {
    outline: none;
  }
}

.panel {
  margin: 1rem;
  margin-bottom: 4rem;
}

.paginate_controls {
  input[type="button"] {
    background-color: v.$primaryBlueDark;
    color: v.$white;
    border: none;
    padding: 1rem;
    font-size: 1.2rem;
    margin-top: 2.5rem;
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.laptop {
      font-size: 1.75rem;
    }
    @include m.desktop {
      font-size: 2rem;
    }
  }
}

.paginate_control_prev {
  border-radius: 2rem 0 0 2rem;
}

.paginate_control_next {
  border-radius: 0 2rem 2rem 0;
}

#buttons {
  display: flex;
  justify-content: center;
}

.btn-gray,
.btn-green {
  display: block;
  width: 60px;
  height: 34px;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  position: relative;
  border-radius: 34px;
  &:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    bottom: 4px;
    background-color: white;
    border-radius: 50%;
  }
  margin-right: 0;
  margin-left: auto;
  @include m.desktopXL {
    margin: inherit auto;
  }
}

.btn-gray {
  background-color: #ccc;
  &:before {
    left: 4px;
  }
}

.btn-green {
  background-color: green;
  &:before {
    right: 4px;
  }
}

