html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  font-family: "Montserrat";
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 2rem;
}
@media (min-width: 320px) {
  h2 {
    font-size: 2.25rem;
  }
}
@media (min-width: 481px) {
  h2 {
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.75rem;
  }
}
@media (min-width: 1025px) {
  h2 {
    font-size: 3rem;
  }
}
@media (min-width: 1201px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 481px) {
  h3 {
    font-size: 1.85rem;
  }
}
@media (min-width: 1025px) {
  h3 {
    font-size: 3rem;
  }
}
@media (min-width: 1201px) {
  h3 {
    font-size: 3.5rem;
  }
}

main p {
  font-size: 1.2rem;
  text-align: center;
  margin: 3rem 0rem;
  color: #DC7DA0;
  font-family: "MontserratSemiBold";
}
@media (min-width: 320px) {
  main p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  main p {
    font-size: 1.5rem;
    margin: 3rem 2rem 0;
  }
}
@media (min-width: 768px) {
  main p {
    margin: 3rem 5rem 0;
  }
}
@media (min-width: 1025px) {
  main p {
    font-size: 2rem;
  }
}
@media (min-width: 1201px) {
  main p {
    font-size: 2.25rem;
    margin: 3rem 10rem 1rem;
  }
}

footer p {
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  footer p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  footer p {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  footer p {
    font-size: 2rem;
  }
}
@media (min-width: 1201px) {
  footer p {
    font-size: 2.25rem;
  }
}

ul {
  padding-left: 0;
}
ul p {
  padding-left: 2rem;
  padding-right: 2rem;
}
@media (min-width: 768px) {
  ul {
    font-size: 2rem;
  }
}

li {
  list-style: none;
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  li {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  li {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  li {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  li {
    font-size: 1.9rem;
  }
}

form p {
  font-size: 1.1rem;
}
@media (min-width: 320px) {
  form p {
    font-size: 1.35rem;
  }
}
@media (min-width: 481px) {
  form p {
    font-size: 1.45rem;
  }
}
@media (min-width: 1025px) {
  form p {
    font-size: 1.65rem;
  }
}
@media (min-width: 1201px) {
  form p {
    font-size: 1.85rem;
  }
}

a {
  font-size: 1.25srem;
  text-decoration: none;
  color: unset;
}
@media (min-width: 1201px) {
  a {
    font-size: 1.5rem;
  }
}

footer a, footer p {
  font-size: 1.25srem;
  text-decoration: none;
  color: unset;
}
@media (min-width: 1201px) {
  footer a, footer p {
    font-size: 1.5rem;
  }
}

.table {
  width: 90%;
  margin: auto;
}
@media (min-width: 481px) {
  .table {
    width: 70%;
  }
}
@media (min-width: 1025px) {
  .table {
    width: 60%;
  }
}
@media (min-width: 1201px) {
  .table {
    width: 50%;
  }
}

td {
  font-size: 1.1rem;
}
@media (min-width: 481px) {
  td {
    font-size: 1.25rem;
  }
}
@media (min-width: 1025px) {
  td {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

button,
.btn {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  button,
.btn {
    font-size: 1.5rem;
  }
}
.directory-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 1201px) {
  .directory-btn {
    align-items: flex-start;
  }
}

.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
}
@media (min-width: 320px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.75rem;
  }
}
label {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  label {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  label {
    font-size: 2rem;
  }
}
.display-none {
  display: none !important;
}

.display-transparent {
  opacity: 0;
  z-index: -1;
}

.background-image {
  position: fixed;
  z-index: -1;
  margin-top: 0rem;
}
.background-image img {
  width: 100%;
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  background-color: #DC7DA0;
  font-family: "CamingoDosProCd-SemiBold";
}
@media (min-width: 320px) {
  .header-top {
    justify-content: space-between;
  }
}
.header-top p {
  font-size: 1.35rem;
  margin: 0;
  margin-bottom: 0.25rem;
  color: #ffffff;
  font-family: "CamingoDosProCd-SemiBold";
}
@media (min-width: 1025px) {
  .header-top p {
    font-size: 1.75rem;
    padding-left: 1rem;
    margin-bottom: 0.5rem;
  }
}
.header-top a {
  height: 100%;
}
.header-top a img {
  height: 100%;
}

.header-phone {
  display: none;
  align-items: center;
}
@media (min-width: 320px) {
  .header-phone {
    display: flex;
  }
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .header-phone .img-container {
    padding-left: 3rem;
  }
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
@media (min-width: 481px) {
  .header-social-media {
    padding-right: 1rem;
  }
}
@media (min-width: 768px) {
  .header-social-media {
    padding-right: 2rem;
  }
}
.header-social-media .img-container {
  height: 2rem;
  margin: 1rem 1.5rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container {
    margin: 3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}
.header-social-media .img-container img {
  height: 100%;
}
.header-social-media a {
  transition: transform 0.5s;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.header-social-media a:hover {
  transform-origin: center center;
  transform: scale(1.2);
  transition: transform 0.5s;
}
.header-social-media a img {
  height: 100%;
}
.header-social-media .mail {
  height: 1.65rem;
}

.header-middle {
  height: 15rem;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-middle {
    height: 20rem;
  }
}
.header-middle .img-container {
  height: 7.5rem;
}
@media (min-width: 320px) {
  .header-middle .img-container {
    height: 10rem;
  }
}
@media (min-width: 768px) {
  .header-middle .img-container {
    height: 14rem;
  }
}
@media (min-width: 1025px) {
  .header-middle .img-container {
    height: 15rem;
  }
}
.header-middle .img-container img {
  height: 100%;
}

.container-fluid {
  display: flex;
  justify-content: center;
}

.navbar {
  background-color: #DC7DA0;
  color: #ffffff;
  font-family: "Fontastique";
  padding-bottom: 0;
  padding-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .container-fluid {
  padding: 0;
}
.navbar .navbar-toggler {
  margin: 1rem auto;
  padding: 0.75rem;
}

@media (min-width: 768px) {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom {
    height: 5rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom {
    height: 7.5rem;
  }
}
.header-bottom .navbar {
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom .navbar {
    flex-direction: row;
  }
}
.header-bottom .navbar .nav-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item {
    width: 20%;
    height: 5rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item {
    height: 7.5rem;
  }
}
.header-bottom .navbar .nav-item .img-container {
  height: 3rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 3.35rem;
    display: none;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 4rem;
    display: block;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 5rem;
  }
}
.header-bottom .navbar .nav-item .img-container img {
  height: 100%;
}
.header-bottom .navbar .nav-item p {
  margin: 1.35rem;
  font-size: 1.25rem;
}
@media (min-width: 481px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.6rem;
    margin: 1rem;
    margin-right: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.9rem;
    margin: 2rem;
  }
}
.header-bottom .navbar a {
  text-decoration: none;
  color: #ffffff;
}
.header-bottom .navbar a:hover {
  color: #ecb9cc;
}

.nav-link {
  display: flex;
  align-items: center;
}

.nav-item {
  width: 100%;
}

nav .navbar-nav .nav-link {
  color: #ffffff;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
}

@media (min-width: 768px) {
  .header-bottom .navbar .nav-item a p {
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item a p {
    padding-left: 1rem;
  }
}

@media (min-width: 768px) {
  .header-bottom .navbar .dropdown a p {
    padding-left: 0rem;
    padding-right: 0.5rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .dropdown a p {
    padding-left: 1rem;
  }
}

.dropdown-toggle::after {
  display: none;
}
@media (min-width: 768px) {
  .dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.5em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
    margin-top: 0.25rem;
  }
}

@media (min-width: 768px) {
  .navbar-nav li:hover > ul.dropdown-menu {
    display: block;
    font-family: "Montserrat";
    min-width: 100%;
  }
  .navbar-nav li:hover > ul.dropdown-menu li {
    font-family: "Fontastique";
  }

  .dropdown-submenu {
    position: relative;
  }

  .dropdown-submenu > .dropdown-menu {
    top: 5rem;
    left: 100%;
    margin-top: -5rem;
  }

  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 5rem;
  }
}
@media (min-width: 768px) and (min-width: 1025px) {
  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 7.4rem;
  }
}
@media (min-width: 768px) {
  .dropdown-menu li {
    padding-bottom: 0.5rem;
  }
  .dropdown-menu li a {
    font-size: 0.75rem;
  }
}
@media (min-width: 768px) and (min-width: 481px) {
  .dropdown-menu li a {
    font-size: 1rem;
  }
}
@media (min-width: 768px) and (min-width: 1025px) {
  .dropdown-menu li a {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) and (min-width: 1201px) {
  .dropdown-menu li a {
    font-size: 1.5rem;
  }
}
/* rotate caret on hover */
.dropdown-menu > li > a:hover:after {
  text-decoration: underline;
  transform: rotate(-90deg);
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: #DC7DA0 !important;
}

footer {
  background-color: #DC7DA0;
}
footer .footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  padding-top: 1rem;
}
@media (min-width: 481px) {
  footer .footer-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
