@use "base/variables" as v;
@use "base/mixins" as m;

.background-image {
  position: fixed;
  z-index: -1;
  margin-top: 0rem;
  img {
    width: 100%;
  }
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  @include m.mobile {
    justify-content: space-between;
  }
  height: auto;
  background-color: v.$primaryBlue;
  font-family: v.$textNarrow;
  p {
    font-size: 1.35rem;
    margin: 0;
    margin-bottom: 0.25rem;
    color: v.$white;
    font-family: v.$textNarrow;
    @include m.desktop {
      font-size: 1.75rem;
      padding-left: 1rem;
      margin-bottom: 0.5rem;
    }
  }
  a {
    height: 100%;
    img {
      height: 100%;
    }
  }
}

.header-phone {
  display: none;
  align-items: center;
  @include m.mobile {
    display: flex;
  }
  & > * {
    margin: 1rem;
  }
  .img-container {
    height: 2rem;
    padding-left: 1rem;
    @include m.laptop {
      padding-left: 3rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-social-media {
  display: flex;
  align-items: center;
  @include m.tablet {
    padding-right: 1rem;
  }
  @include m.laptop {
    padding-right: 2rem;
  }
  .img-container {
    height: 2rem;
    margin: 1rem 1.5rem;
    @include m.laptop {
      margin: 3rem;
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    img {
      height: 100%;
    }
  }
  a {
    transition: transform 0.5s;
    display: flex;
    align-items: center;
    color: v.$white;
    &:hover {
      transform-origin: center center;
      transform: scale(1.2);
      transition: transform 0.5s;
    }
    img {
      height: 100%;
    }
  }
  .mail {
    height: 1.65rem;
  }
}

.header-middle {
  height: 15rem;
  @include m.laptop {
    height: 20rem;
  }
  // background-color: v.$primaryBlueDark;
  // background-image: url('/build/img/hero/logoBg.webp');
  // background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  .img-container {
    height: 7.5rem;
    @include m.mobile {
      height: 10rem;
    }
    @include m.laptop {
      height: 14rem;
    }
    @include m.desktop {
      height: 15rem;
    }
    img {
      height: 100%;
    }
  }
}

.container-fluid {
  display: flex;
  justify-content: center;
}

.navbar {
  background-color: v.$primaryBlueDark;
  color: v.$white;
  font-family: v.$subtitle;
  padding-bottom: 0;
  padding-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  // @include m.desktop {
  //   border-bottom: 2.5rem solid v.$primaryBlueDark;
  // }

  .container-fluid {
    padding: 0;
  }
  .navbar-toggler {
    margin: 1rem auto;
    padding: 0.75rem;
  }
}

@include m.laptop {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  width: 100%;
  height: auto;
  @include m.laptop {
    height: 5rem;
  }
  @include m.desktop {
    height: 7.5rem;
  }
  display: flex;
  align-items: center;
  .navbar {
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
    justify-content: space-evenly;
    align-items: center;
    .nav-item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      @include m.laptop {
        width: 20%;
        height: 5rem;
      }
      @include m.desktop {
        height: 7.5rem;
      }
      .img-container {
        height: 3rem;
        @include m.laptop {
          height: 3.35rem;
          display: none;
        }
        @include m.desktop {
          height: 4rem;
          display: block;
        }
        @include m.desktopXL {
          height: 5rem;
        }
        img {
          height: 100%;
        }
      }
      p {
        margin: 1.35rem;
        font-size: 1.25rem;
        @include m.tablet {
          font-size: 1.5rem;
        }
        @include m.laptop {
          font-size: 1.6rem;
          margin: 1rem;
          margin-right: 0;
        }
        @include m.desktop {
          font-size: 1.7rem;
        }
        @include m.desktopXL {
          font-size: 1.9rem;
          margin: 2rem;
        }
      }
    }
    a {
      text-decoration: none;
      color: v.$white;
      //   color: v.$secondaryGray;
      &:hover {
        color: lighten($color: v.$primaryBlue, $amount: 15);
      }
    }
  }
}

.nav-link {
  display: flex;
  align-items: center;
}

.nav-item {
  width: 100%;
}

nav .navbar-nav .nav-link {
  color: v.$white;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
}

.header-bottom .navbar .nav-item a p {
  // font-size: 2rem;
  @include m.laptop {
    padding: 0;
    margin: 0;
  }
  @include m.desktop {
    padding-left: 1rem;
  }
}

.header-bottom .navbar .dropdown a p {
  @include m.laptop {
    padding-left: 0rem;
    padding-right: 0.5rem;
  }
  @include m.desktop {
    padding-left: 1rem;
  }
}

.dropdown-toggle::after {
  display: none;
  @include m.laptop {
    display: inline-block;
    margin-left: 0.5em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
    margin-top: 0.25rem;
  }
}

@include m.laptop {
  .navbar-nav li:hover > ul.dropdown-menu {
    display: block;
    font-family: v.$text;
    min-width: 100%;
    li {
      font-family: v.$subtitle;
    }
  }
  .dropdown-submenu {
    position: relative;
  }
  .dropdown-submenu > .dropdown-menu {
    top: 5rem;
    left: 100%;
    margin-top: -5rem;
  }

  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 5rem;
    @include m.desktop {
      top: 7.4rem;
    }
  }

  .dropdown-menu li {
    padding-bottom: 0.5rem;
    a {
      font-size: 0.75rem;
      @include m.tablet {
        font-size: 1rem;
      }
      @include m.desktop {
        font-size: 1.25rem;
      }
      @include m.desktopXL {
        font-size: 1.5rem;
      }
    }
  }
}

/* rotate caret on hover */
.dropdown-menu > li > a:hover:after {
  text-decoration: underline;
  transform: rotate(-90deg);
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: v.$primaryBlue !important;
}
