@use "base/variables" as v;
@use "base/mixins" as m;

.spanish {
  font-size: 1em;
  font-weight: bold;
}
.french {
  font-size: 0.9em;
}
.form-white {
  color: v.$white;
}

.form-header {
  // background: transparent url("/build/img/phenotype/header.png") no-repeat
  //   scroll left center;
  background-color: v.$primaryBlueDark;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  @include m.tablet {
    padding: unset;
    flex-direction: row;
    justify-content: space-around;
    padding: 2rem;
  }
  .form-header-logo {
    height: 3rem;
    margin-bottom: 2rem;
    @include m.tablet {
      margin-bottom: 0;
      // height: 12.5rem;
    }
    @include m.desktop {
      height: 2.5vw;
    }
    img {
      height: 100%;
    }
  }
  .form-header-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 4vw;
    text-align: center;
    padding: inherit 10vw;
    @include m.tablet {
      font-size: 3vw;
      padding: unset;
      padding-left: 5vw;
      padding-right: 5vw;
    }
    @include m.desktop {
      font-size: 2.5vw;
    }
  }
}

.form-phenotype {
  margin: 3rem 2.5rem;
  padding: 0;
  @include m.tablet {
    margin: 3rem 5rem;
  }
  .form-top,
  .form-characteristics-text {
    display: flex;
    margin: 4rem auto;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
  }
  .form-top-element,
  .form-characteristics-element {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    margin: 1rem auto;
    label {
      margin-left: 0%;
    }
    @include m.tablet {
      label {
        margin-left: 50%;
      }
    }
    @include m.laptop {
      display: flex;
      width: 33.3333333%;
      align-items: flex-start;
      justify-content: flex-start;
      margin: unset;
      label {
        margin-left: 0%;
      }
    }
  }
  .col-md-6 {
    @include m.laptop {
      width: 50%;
    }
  }
  .form-control-phenotype {
    border: 0px solid transparent;
    border-bottom: 2px solid v.$primaryBlueDark;
    margin-left: 1.25rem;
    font-size: 1.5rem;
    width: 80%;
    &:focus-visible {
      outline: none;
    }
    @include m.tablet {
      width: 50%;
      margin-left: 2rem;
      font-size: 2.5rem;
    }
  }
  .form-control-phenotype-msg {
    &:focus-visible {
      outline: none;
    }
  }
  textarea {
    font-size: 2rem;
  }
  .form-subtitle {
    background-color: v.$primaryBlueDark;
    display: flex;
    padding: 2rem;
    margin: 6rem auto 4rem auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 4vw;
    @include m.tablet {
      flex-direction: row;
      font-size: 2rem;
    }
  }
  .form-subtitle-grid {
    background-color: v.$primaryBlueDark;
    display: flex;
    flex-direction: column;
    font-size: 2rem;
    padding: 2rem;
    margin: 2rem auto;
    font-size: 4vw;
    text-align: center;
    @include m.tablet {
      text-align: left;
      flex-direction: row;
      align-items: baseline;
      font-size: 2rem;
      justify-content: space-between;
    }
  }
  .space {
    width: 5px;
  }
  // .form-characteristics-text {
  //   display: flex;
  //   margin: 2rem auto;
  // }
  // .form-characteristics-element {
  //   display: flex;
  //   align-items: flex-start;
  // }
  .form-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    @include m.laptop {
      grid-template-columns: repeat(2, 1fr);
      gap: 4rem;
    }
  }
  .form-btn {
    margin-top: 5rem;
  }
  .form-column {
    display: flex;
    flex-direction: column;
    .form-grid-element {
      width: 50%;
    }
  }
  .form-check {
    margin: 2rem auto;
    display: flex;
    align-items: flex-start;
  }
  .form-check-label {
    .french,
    .spanish {
      display: inline-block;
    }
    width: 100%;
  }
  .form-phenotype-msg {
    width: 100%;
    @include m.desktop {
      width: 50%;
    }
    .form-subtitle {
      flex-direction: column;
      align-items: flex-start;
      text-align: center;
      @include m.desktop {
        text-align: left;
      }
    }
  }
  .form-control-phenotype-msg {
    width: 100%;
    font-size: 2.5rem;
    border: 2px solid v.$primaryBlueDark;
    height: 30rem;
    margin-bottom: 2rem;
    padding: 1rem;
  }
  .form-phenotype-text {
    font-size: 3.5vw;
    margin-bottom: 2rem;
    @include m.tablet {
      font-size: 1.75rem;
    }
  }
}

// Particular CSS for checkbuttons

.form-check-input {
  -webkit-appearance: auto !important;
  appearance: auto !important;
  &:focus {
    border-color: v.$primaryBlueDark;
    box-shadow: 0 0 0 0 lighten(v.$primaryBlueDark, 10%);
  }
  &:checked {
    background-color: v.$primaryBlueDark;
    border-color: v.$primaryBlueDark;
    &[type="radio"] {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='M6 10l3 3l6-6'/%3e%3c/svg%3e");
    }
  }
  &[type="radio"] {
    border-radius: unset;
    width: 2.5rem;
    height: 2.5rem;
    border: 2px solid v.$primaryBlueDark;
  }
}