@use "base/variables" as v;
@use "base/mixins" as m;

.hero-img {
  width: 100%;
  img {
    width: 100%;
    height: 100%;
  }
  .img-container {
    position: relative;
    height: 50vw;
    // img {
    //   position: absolute;
    //   top: 0px;
    //   left: 0px;
    //   animation-name: imagefade;
    //   animation-timing-function: ease-in-out;
    //   animation-iteration-count: infinite;
    //   animation-duration: 10.5s; // a = 3, b = 0.5, n = 3
    //   &:nth-of-type(1) {
    //     animation-delay: 7s;
    //   }
    //   &:nth-of-type(2) {
    //     animation-delay: 3.5s;
    //   }
    //   &:nth-of-type(3) {
    //     animation-delay: 0;
    //   }
    // }
  }
  position: relative;
  .hero-text {
    position: absolute;
    height: 100%;
    margin-left: 50%;
    margin-right: 10%;
    bottom: 0;
    color: v.$white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p {
      color: v.$white;
      margin: 0;
      font-size: 1rem;
      @include m.mobile {
        font-size: 1.3rem;
      }
      @include m.tablet {
        font-size: 1.5rem;
        text-align: left;
        span {
          font-size: 1.2em;
          display: block;
          padding-left: 1.5rem;
        }
      }
      @include m.laptop {
        font-size: 2rem;
        text-align: left;
        span {
          font-size: 1.5em;
          display: block;
          padding-left: 1.5rem;
        }
      }
      @include m.desktop {
        font-size: 2.75rem;
        text-align: left;
        span {
          font-size: 1.5em;
          display: block;
          padding-left: 1.5rem;
        }
      }
      @include m.desktopXL {
        font-size: 3rem;
        text-align: left;
        span {
          font-size: 1.5em;
          display: block;
          padding-left: 1.5rem;
        }
      }
    }
    a {
      margin-top: 1rem;
      border: 1px solid v.$white;
      padding: 0.5rem 1rem;
      margin-left: auto;
      margin-right: auto;
      color: v.$white;
      transition: all 0.25s;
      &:hover {
        color: v.$white;
        background-color: darken(v.$primaryBlueDark, 5%);
        transition: all 0.25s;
      }
      @include m.mobile {
        font-size: 1.3rem;
        margin-top: 2rem;
      }
      @include m.tablet {
        font-size: 1.5rem;
        margin-top: 3rem;
      }
      @include m.laptop {
        font-size: 2rem;
        margin-top: 6rem;
      }
      @include m.desktop {
        font-size: 2.5rem;
        margin-top: 10rem;
        padding: 1rem 3rem;
      }
      @include m.desktopXL {
        font-size: 3rem;
        margin-top: 15rem;
        padding: 1rem 5rem;
      }
    }
  }
}

.hero-button {
  display: flex;
  justify-content: center;
}

