@use "base/variables" as v;
@use "base/mixins" as m;

.home-info {
  width: 80%;
  margin: 2rem auto;

  .img-container {
    width: 17rem;
    margin: 3rem auto 0 auto;

    img {
      width: 100%;
    }
  }

  @include m.mobile {
    .img-container {
      width: 20rem;
    }

    margin: 4rem auto;
  }

  @include m.tablet {
    margin: 6rem auto;
  }

  @include m.desktop {
    .img-container {
      width: 30rem;
    }

    margin: 10rem auto;
  }

  @include m.desktopXL {
    margin: 15rem auto;
  }

  .heart-container {
    width: 10rem;
    margin: auto;
    margin-bottom: 2rem;

    @include m.tablet {
      width: 12.5rem;
    }

    img {
      padding: 2rem;
      padding-top: 0;
      width: 100%;
    }
  }

  .home-info-text {
    @include m.tablet {
      margin-bottom: 4rem;
    }

    @include m.laptop {
      margin-top: 7.5rem;
      margin-bottom: 7.5rem;
    }
  }
}

.home-section {
  background-color: v.$primaryBlue;
  text-align: right;
  padding: 2rem;
  color: v.$white;
  font-family: v.$text;

  span {
    font-size: 1.25em;
  }

  @include m.tablet {
    padding-right: 7.5rem;
  }
}

.home-programs {

  .home-section-program-1,
  .home-section-program-2 {
    display: grid;

    .home-program-img {
      order: -1;
      width: 100%;

      img {
        width: 100%;
      }
    }

    @include m.tablet {
      margin-bottom: 5rem;
    }
  }

  .home-program-text {
    background-color: v.$secondaryGray;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem auto;

    .home-program-description {
      padding: inherit 2rem;

      @include m.desktop {
        margin-top: 3rem;
        margin-bottom: 3rem;
      }

      @include m.desktopXL {
        margin-top: 7rem;
        margin-bottom: 7rem;
      }

      h3 {
        color: v.$primaryPurple;
        text-align: center;
        padding-top: 3rem;
        font-weight: bold;
      }

      p {
        color: v.$white;
      }
    }

    .home-program-btn {
      color: v.$white;
      border: 1px solid v.$white;
      padding: 1rem 4rem;
      transition: all 0.25s;

      &:hover {
        background-color: darken($color: v.$secondaryGray, $amount: 5);
        transition: all 0.25s;
      }

      // @include m.tablet {
      //   margin-top: 3rem;
      //   margin-bottom: 3rem;
      // }
    }
  }

  @include m.tablet {
    margin: 5rem 7rem 7rem 7rem;
  }

  @include m.laptop {
    width: 60rem;
    margin: 20rem auto 10rem auto;

    .home-section-program-1,
    .home-section-program-2 {
      width: 65rem;
      margin-top: 15rem;
      position: relative;

      .home-program-img {
        order: 0;
        width: 27.5rem;

        img {
          width: 100%;
        }
      }

      .home-program-text {
        display: grid;

        h3 {
          margin: 2rem;
          padding: 0;
        }

        p {
          margin: 2rem;
          padding: 0;
        }

        .home-program-btn {
          padding: 0.5rem 1rem;
          text-align: center;
          margin: 40% auto 7% auto;
          width: 12rem;

          @include m.desktop {
            margin: 35% auto 5% auto;
            width: 10rem;
          }
        }
      }
    }

    .home-section-program-1 {
      .home-program-img {
        position: absolute;
        left: -3.5rem;
        top: -11rem;

        .img-rect-1 {
          width: 100%;
          height: 100%;
          background-color: v.$primaryBlueDark;
          position: relative;

          .img-rect-2 {
            position: absolute;
            right: 2rem;
            width: 100%;
            height: 100%;
            background-color: v.$primaryBlueDark;
          }
        }
      }

      .home-program-text {
        .home-program-btn {
          order: -1;
        }

        grid-template-columns: 3fr 5fr;
      }
    }

    .home-section-program-2 {
      .home-program-img {
        position: absolute;
        top: -11rem;
        right: -3.5rem;
      }

      .home-program-text {
        grid-template-columns: 5fr 3fr;
      }
    }
  }

  @include m.desktop {
    width: 120rem;

    .home-section-program-1 {
      .home-program-img {
        left: -1.5rem;
        top: -15rem;

        @include m.desktop {
          top: -11vw;
        }
      }

      .home-program-text {
        .home-program-btn {
          order: -1;
        }

        grid-template-columns: 3fr 5fr;
      }
    }

    .home-section-program-2 {
      .home-program-img {
        position: absolute;
        top: -15rem;
        right: -1.5rem;
      }

      .home-program-text {
        grid-template-columns: 5fr 3fr;
      }
    }

    .home-section-program-1,
    .home-section-program-2 {
      margin-top: 20rem;
      width: 100%;

      .home-program-img {
        width: 40%;
      }
    }

    .home-program-btn {
      width: 15rem !important;
      font-size: 2rem;
    }
  }

  .home-section-program-3 {
    .home-program-img {
      top: -13vw;
    }
  }

  @include m.desktopXL {
    .home-section-program-1 {
      .home-program-img {
        top: -7.5vw;
      }
    }

    .home-section-program-2 {
      .home-program-img {
        top: -8.5vw;
      }
    }

    .home-section-program-3 {
      .home-program-img {
        top: -8.5vw;
      }
    }

    .home-section-program-1,
    .home-section-program-2 {
      margin-top: 30rem;
    }
  }
}

.grid-button {
  margin: 1rem inherit 5rem inherit;

  a {
    font-size: 1.4rem;

    @include m.tablet {
      font-size: 1.6rem;
    }
  }

  @include m.tablet {
    margin-top: 4rem;
  }

  @include m.laptop {
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 2rem;
  }
}

.home-section-program-1 {
  .grid-button {
    display: flex;
    order: 0;

    @include m.laptop {
      order: -1;
    }
  }
}

.home-program-text {
  @include m.desktop {
    h3 {
      font-size: 2.5rem;
    }

    p {
      font-size: 1.6rem;
    }
  }

  @include m.desktopXL {
    h3 {
      font-size: 2.75rem;
    }

    p {
      font-size: 2rem;
    }
  }
}

// IMAGE WITH TEXT SECTION

.img-text {
  margin: 1rem;
  display: grid;
  gap: 1rem;

  .img-text-img {
    width: 100%;
    padding: 10vw;

    img {
      width: 100%;
    }
  }

  .img-text-text {
    padding: 0 5vw;
    text-align: center;
  }

  @include m.tablet {
    .img-text-text {
      padding-bottom: 8rem;
    }

    .img-text-img {
      padding: 5rem;
    }
  }

  @include m.desktop {
    .img-text-text {
      text-align: justify;
      display: flex;
      flex-direction: column;
      justify-items: center;
      align-items: center;
    }

    .img-text-img {
      width: 70vw;
      margin: auto;
    }
  }

  @include m.desktopXL {
    grid-template-columns: 1fr 1fr;
    max-width: 160rem;
    margin: auto;
    padding-top: 5rem;

    .img-text-img {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: baseline;
    }

    .img-text-text {
      padding: 0;
      padding-top: 5rem;
      padding-right: 5rem;
      padding-bottom: 5rem;

      h3 {
        text-align: center;
        padding-bottom: 2.5rem;
        font-size: 3rem;
      }

      p {
        margin: 0;
        padding: 0;
        font-size: 1.75rem;
        padding-bottom: 1rem;
      }
    }
  }
}

// SECTION IMAGE PROCESS

.img-process {
  display: none;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15rem;
  img {
    width: 70vw;
  }
  @include m.laptop {
    display: flex;
  }
  @include m.desktopXL {
    margin-top: 7.5rem;
    img {
      max-width: 100rem;
    }
  }
}

// Under construction

.temporary {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: calc(1.5rem + 1.5vw);
  text-align: center;
  margin: 1rem;
  color: v.$primaryBlueDark;
  font-family: v.$subtitle;
}